body {
  margin: 0;
  padding: 0;
  font-family: Acme, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(left, rgb(211, 231, 223) , rgb(179, 230, 217));
  background: linear-gradient(to right, rgb(211, 231, 223) , rgb(179, 230, 217));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
    margin:0 !important;
}

.bg-light-red{
    background-color: #122f2c;
    color:#fff;
}
.btn:hover{
    background-color: #0c1b17;
}
.delete {
    float: right;
    padding: 3px 5px;
    -webkit-transform:translateY(-3px);
            transform:translateY(-3px);
    background-color: rgb(117, 38, 18);
    color: #fff;
    border-radius: 2px;
    text-align: center;
}

.delete:hover{
    background-color: rgb(102, 11, 11)
}
.b--gray:hover {
    border-left: 4px solid #122f2c !important;
}
